body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
:root {
  --blue-shade: #0083e2;
  --light: rgb(153, 150, 150);
  --light-btn: #f0f0f0;
}
li{
  list-style: none;
}
/* ---------------------------- */


/* ---------------------------- */

/* -----------------NAVBAR---------------- */
nav {
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px,
    rgba(155, 149, 149, 0.24) 0px 1px 1px;
}
nav li, nav button{
  text-decoration: underline;
}
.nav_left {
  /* background: yellow; */
  width: 40%;
  justify-content: space-around;
  align-items: center;
  display: flex;
  text-transform: uppercase !important;
}
.nav_left i {
  font-size: 2rem;
  color: var(--light);
}
.navbar {
  display: flex;
  width: 100%;
  justify-content: space-around;
  /* background: green !important; */
  padding: 1rem auto;
  /* font-size: rem; */
}
.collapse {
  /* background: rebeccapurple; */
  display: flex;
  justify-content: space-between;
  /* width: 100%; */
}
.navbar form {
  width: 30%;
}
.navbar ul li {
  letter-spacing: 0.2rem;
}

.navbar .nav_left .btn-upload {
  background: #001c52;
  color: white;
}
.navbar .nav_left .btn-connect {
  background: var(--light-btn);
  border: 1px solid gray;
}
.navbar form div {
  position: relative;
}
.navbar form div i {
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--light);
}
/* ======================HOME======================= */

.home {
  margin: 3rem auto;
  padding: 2rem;
}
.home .left_container button {
  font-weight: bold;
  padding: 0.6rem 1.6rem;
  color: white;
}
.home .left_container .btn1 {
  background: var(--blue-shade);
}
.home .left_container h1 {
  color: var(--blue-shade);
  font-weight: bolder;
}
.home .left_container p {
  text-transform: uppercase;
  color: #8b8888;
}
.home .left_container .btn2 {
  border: 1px solid var(--blue-shade);
  color: var(--blue-shade);
  background: var(--light-btn);
}
.home .right_container {
  box-shadow: 0 -19px 19px 2px rgba(0, 0, 0, 0.1);
}

/* =============================SLIDESHOW===================== */

.carouselImg {
  margin: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  overflow: hidden;
  transition: transform ease-out 0.3s;
  transition-delay: 0.4s;
}
.carouselImg img {
  width: 100%;
  height: 90%;
}
.carouselImg:hover img {
  transform: scale(1.2);
}
.carouselImg .imgContent {
  z-index: 9;
  background: black;
  text-align: center;
  color: white;
  text-transform: none;
  width: 100%;
  padding: 1rem;
  text-decoration: none !important;
}
.imgContent a {
  padding: 0.5rem 2.1rem;
  border: 1px solid white;
  color: white;
  border-radius: 6px;
}

/* ==================SECTION 3================ */

.section3 {
  margin: 4rem auto;
}
.section3 .card {
  width: 22rem;
  border-radius: 10px;
}
.section3 .left_side .card img {
  border-radius: 10px;
}
.section3 .left_side .card .card-body img {
  width: 3rem;
  height: 3rem;
}
.section3 .left_side .card .card-body .right {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 1rem;
}
.section3 .left_side .right span {
  font-size: 0.8rem;
  margin-left: 1rem;
  color: var(--light);
}
.section3 .middle{
  margin-left: 8rem;
}
.section3 .middle .middle_card {
  display: flex !important;
  /* height: 15rem; */
  /* background: green; */
  position: relative;
  /* justify-content: center; */
  align-items: center;
}

.section3 .middle .d-flex img {
  width: 3rem;
  height: 3rem;
}
.section3 .middle_img {
  width: 10rem !important;
  height: 15rem !important;
  border-radius: 1rem;
}
.section3 .middle .card_right {
  /* background: yellow; */
  height: 100% !important;
  padding: 2rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.section3 .middle .card_right .d-flex {
  align-items: center;
}
.card_right button {
  padding: 0.4rem 1rem;
  border-radius: 1rem;
  margin: 1rem auto;
  color: var(--blue-shade);
  border: 1px solid var(--blue-shade);
}
.latest_uploads{
  /* background: yellow; */
  border-left: 1px solid #cecbcb;
}
.latest_uploads .icons{

}
.latest_uploads .icons img
{
  width: 3.1rem;
  height: 3rem;
}
.latest_uploads .imgIcon{
  position: relative;
}
.latest_uploads .imgIcon span{
  position: absolute;
  left: 0;
  top: -4px;
  padding: .1rem .5rem;
  background: black;
  color: white;
  border-radius: 50%;
  font-size: .8rem;
}
.green_btn{
  padding: .2em .9rem !important;
  border: 2px solid green !important;
  color: green;
  border-radius: .3rem;
  font-size: .8rem;
}


/* =========================================POPULAR CREATORS============================================= */
.header{
  display: flex;
  justify-content: space-between;
  /* background: yellow; */
}
.bg{
  background: #F4F5F6;
padding: 4rem 1rem;
}
.creators{
  margin: 8rem auto;
 padding: 4rem auto;
}
.creators ul li{
  text-decoration: none !important;
}
.creators .timeframe button{
  background: white;
  color: black;
  width: 20rem;
  text-align: left;
}
.card_container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  margin: 4rem auto;
}
.creators .card, .creators .card-header{


  
  background: white;
}
.creators .card img{
  width: 5rem;
}
.creators .card p span{
  color: gray;
}
.creators .card{
  /* background: yellow; */
  width: 15rem;
  padding: 1rem;
  border:none;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
transition: all ease .3s;

}
.card_container .card-header span{
  background: teal;
  padding: .4rem .8rem;
  border-radius: 1rem;
  font-size: .8rem;
  color: white;
}
.card_container .card:hover .card-body{
  transform: scale(1.2);
transition-delay: .2s;
cursor: pointer;

}

.collections .card{
  display: flex;
  border: none;
}
.collections .card .card-body{
  display: flex;
  justify-content: space-evenly;
   align-items: center;
   padding: 0;

}
.collections .card .card-body img{
  width: 7rem;
  margin:.6rem auto;
  margin-top: 1rem;
  border-radius: 1rem;
}
.collections .card-footer{
  border: none;
  display: flex;
  /* justify-content: space-between; */
  align-items: center;
  background: white;
}
.collections .card-footer p{
  margin-top: 1.2rem;
}

.collections .card-footer span{
  font-weight: bold;
}
.collections .card-footer img{
  width: 3rem;
  height: 3rem;
}
.collections .card-footer button{
  margin-left: 1rem;
}
/* ==================================DISCOVER=================== */
.discover{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 9rem auto;
}
.discover .nav_filter{
  display: flex !important;
  justify-content: center;
  align-items: flex-end;
  /* background: yellow; */
  width:100%;
}
.discover .header ul{
  margin-top: 1rem;
}
.discover .header ul li{
  margin-left:1rem !important; 
  list-style: none;
color: gray;
  text-transform: none;
  font-weight: bold;
}
.discover .games{
  display: flex;
  text-align: center;
  margin:3rem auto;
}
.discover .games .card img{
  /* height: 10rem; */
  width: 100%;
}
.discover .games .card {
  height: 12rem !important;
  overflow: hidden;
  width: 100%;

}
.discover .games a{
  font-weight: bold;
  text-decoration: none;
  margin: 2rem 1rem;
}
.discover .header .filter_btn button{
  background: #001C52;
  color: white;
  border-radius: 1rem;
  text-decoration: none;
}
.discover .header .active{
  background: #353945;
  color: white;
  border-radius: 1rem;
  padding: .2rem .5rem;
}
.discover .game_card{
  border: 1px solid #f1f1f0;
 
}
.discover .light_btn{
  padding: 0.4rem 1rem;
  border-radius: 1rem;
  margin: 1rem auto;
  color: var(--blue-shade);
  border: 1px solid var(--blue-shade);
  margin: auto;
}
.exta_content {
  color: gray;
  margin:6rem auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.exta_content h1{
  color: #001C52;
}
.exta_content button{
  border-radius:.3rem !important ;
  font-weight: bold;
  margin:3rem auto;
}
.exta_content .btn{
  background: #001C52;
  color: white;
}
.exta_content .light_btn{
  padding: 0.4rem 1rem;
  border-radius: 1rem;
  margin: 1rem auto;
  color: var(--blue-shade);
  border: 1px solid var(--blue-shade);
  margin: auto;
  background: var(--light-btn);
  margin-left: 1rem;
}
.exta_content img{
  width: 30rem;
}
/* ---------------------------------FOOTER------------------------ */
.footer{
  margin-top: 8rem ;
}

.copyrights{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: .8rem;
}
.footer .form {
  width: 90%;
}
.footer .form input{
  border-radius: 1.2rem;
  width: 100%;
  padding: .4rem ;
  background: var(--light-btn);
  border: 2px solid rgb(165, 162, 162);
  outline: none;
}
.footer ul li a{
  text-transform: none;
  font-weight: bold;
  text-decoration: none;
  color: gray;


}

.footer ul .dark_li a{
  color: #391C52 !important;
}